import { toast } from 'react-toastify'

export const toastAddFavorite = (title: string): void => {
	toast.success(` "${title}" `, {
		position: 'top-right',
		autoClose: 1200,
		hideProgressBar: true,
		closeOnClick: true,
		pauseOnHover: true,
		draggable: true,
		progress: undefined,
		theme: 'dark'
	})
}

